  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  display: block;
  position: absolute;
}

.loader:before {
  content: "";
  width: 15px;
  height: 15px;
  color: #3b2d5a;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: animloader 1s linear infinite alternate;
  display: block;
  position: relative;
}

@keyframes animloader {
